.message {
  display: flex;
  align-items: flex-end;
  margin: 10px;
}

.avatar {
  height: 50px;
  width: 50px;
  border-radius: 50px;
}

.content {
  padding: 10px;
  border-radius: 5px 5px 5px 0px;
  background-color: rgba(15, 150, 203, 0.3);
}

.angle {
  width: 15px;
  height: 15px;
  background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, rgba(15, 150, 203, 0.3) 0);
}

.text {
  min-width: 150px;
  max-width: 300px;
  word-wrap: break-word;
  color: #bbffbb;
}

.name {
  font-size: 20px;
  font-weight: bold;
  color: #bbffbb;
}

.time {
  display: flex;
  justify-content: flex-end;
  color: #bbffbb;
}